import React, { Component } from 'react';
import TodoScore from './TodoScore';

class TodoScoreList extends Component {
    
  shouldComponentUpdate(nextProps, nextState) {
    return this.props.todos !== nextProps.todos;
  }

  render() {
    const { todos } = this.props;

    const todoList = todos.map(
        ({ id, score }) =>(
            <TodoScore
                id={id}
                score={score}
                key={id}
            />
        )
    );

    return (
        <div className="liss">
            {todoList}
        </div>
    );
  }
}

export default TodoScoreList;