import React from 'react'
import './Form.css'
import { AwesomeButton } from 'react-awesome-button'
import 'react-awesome-button/dist/themes/theme-blue.css'

const Form = ({ onCreate, onRemove }) => {
    return (
        <div className="form">
            <AwesomeButton size="small" type="primary" onPress={onCreate}>
                추가
            </AwesomeButton>
            <span className="cross"></span>
            <AwesomeButton size="small" type="secondary" onPress={onRemove}>
                제외
            </AwesomeButton>
        </div>
    )
}

export default Form
