import React, { Component } from 'react'
import TodoListTemplate from './components/TodoListTemplate'
import Form from './components/Form'
import Form2 from './components/Form2'
import TodoItemList from './components/TodoItemList'
import TodoScoreList from './components/TodoScoreList'
class App extends Component {
    id = 4 // 이미 0,1,2,3 가 존재하므로 4으로 설정

    state = {
        todos: [
            { id: 0, text: 'P1', score: '0', change: '' },
            { id: 1, text: 'P2', score: '0', change: '' },
            { id: 2, text: 'P3', score: '0', change: '' },
            { id: 3, text: 'P4', score: '0', change: '' },
        ],
        shistory: [],
    }

    handleCreate = () => {
      const { todos } = this.state
      this.setState({
          todos: todos.concat({
              id: this.id++,
              text: 'P' + this.id,
              score: '0',
          }),
      })
  }
  handleCalculate = () => {
    console.log("cal");
}

  handleIncrease = () => {
      const { todos } = this.state
      this.setState({
          todos: todos.concat({
              id: this.id++,
              text: 'P' + this.id,
              score: '0',
          }),
      })
  }

    handleRemove = () => {
        const { todos } = this.state
        this.id--
        this.setState({
            todos: todos.filter((todo) => todo.id !== this.id),
        })
    }

    handleRemove2 = (id) => {
      const { todos } = this.state
      this.setState({
          todos: todos.filter((todo) => todo.id !== id),
      })
  }
    render() {
        const { todos } = this.state
        const { handleCreate, handleRemove, handleRemove2, handleIncrease, handleCalculate } = this

        return (
            <TodoListTemplate
            form={
                <Form
                    onRemove={handleRemove}
                    onCreate={handleCreate}
                />
            }
            form2={
                <Form2
                    onCalculate={handleCalculate}
                />
            }
                children={
                    <TodoItemList todos={todos} onPress1={handleIncrease} />
                }
                children2={
                    <TodoScoreList todos={todos} onRemove={handleRemove2} />
                }
            ></TodoListTemplate>
        )
    }
}

export default App
